window.addEventListener('DOMContentLoaded', () => {
    animate()
    window.addEventListener('resize', () => {
        animate()
    })
})

function animate() {
    const tlLines = gsap.timeline({
        scrollTrigger: {
            trigger: '.who__nums',
            start: 'top 70%',
            end: 'bottom 80%',
            scrub: 1
        }
    })
    tlLines.to('.nums', {
        xPercent: -100
    })


    gsap.to('.cool__tag', {
        opacity: 1,
        y: 0,
        x: 0,
        stagger: -0.2,
        duration: .05,
        scrollTrigger: {
            trigger: '.cool__tags',
            start: 'top bottom',
        }

    })
}
